$background-light: #f7f8fa;
$background-dark: #2d2d2d;
$border-light: #ccc;
$border-dark: #444;

$color-light: #fff;
$color-dark: #444444;
$heading-size: 20px;
$subheading-size: 16px;
$text-size: 14px;
$menubar-width: 90px;
$header-height: 76px;

.stla-booster-wrapper {
  background: #ffffff;
  color: #444444;
  cursor: default;
  height: 100%;
  margin: 0 !important;
  min-width: 0;
  overflow: hidden;
  position: fixed;
  z-index: 100099;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  * {
    box-sizing: border-box;
  }
}

// scrollbars mixins

@mixin scrollbars($size, $foreground-color, $background-color) {
  //  scrollbar design
  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px $background-color;
    background-color: $background-color;
  }
  &::-webkit-scrollbar {
    width: 6px;
    background-color: $background-color;
  }

  &::-webkit-scrollbar-thumb {
    background-color: $foreground-color;
  }

  // For Internet Explorer
  & {
    scrollbar-face-color: $foreground-color;
    scrollbar-track-color: $background-color;
  }
}

// menubar
.stla-panels-menubar {
  width: $menubar-width;
  background-color: $background-dark;
  height: calc(100vh - 76px);
  position: fixed;
  left: 0;
  top: $header-height;
  overflow: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

  @include scrollbars(10px, #ccc, $background-light);

  // menubar btn
  .menubar-btn {
    color: $color-light;
    padding: 8px;
    font-size: $text-size;
    text-align: center;
    border-bottom: 1px solid $border-dark;
    border-left: 2px solid transparent;
    cursor: pointer;
  }

  .menubar-btn.settingsBtn {
    margin: 20px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 3px;
    border: 1px solid #ccc;

    .dashicon {
      font-size: 30px;
      width: 30px;
      height: 30px;
    }
  }

  .menubar-btn.settingsBtn.active-panel {
    background: var(--wp-admin-theme-color);
  }

  .active-panel {
    background-color: rgba(255, 255, 255, 0.2);
    border-left-color: $border-light;
  }

  .menubar-btn-txt {
    margin: 0px;
    font-size: inherit;
  }

  .dashicons {
    font-size: 20px;
    width: 20px;
    height: 20px;
  }

  .stla-panel-btn-margin-auto {
    margin-top: auto;
  }
}

// panels
.stla-panels {
  .stla-panel {
    width: calc(100vw - 91px);
    height: 100vh;
    margin-left: $menubar-width;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
  }

  // panel content area
  .stla-panel-contentarea {
    height: calc(100vh - 76px);
    padding: 20px 40px;
    margin-top: 76px;
    margin-left: 400px;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100%;

    @include scrollbars(10px, #ccc, $background-light);

    .stla-responsive-toolbar {
      .stla-page-select {
        font-size: $subheading-size;
      }

      .stla-page-select-dropdown {
        vertical-align: middle;
        margin-left: 5px;

        .components-button {
          background: #f7f8fa;
          border: 1px solid #ccc;
          color: #ccc;
          width: 30px;
          height: 30px;
          min-width: unset;
          padding: 0px;
        }
      }

      .dashicon {
        font-size: 30px;
        width: 40px;
        height: 40px;
        padding: 5px;
      }
    }

    .stla-settings-contentarea {
      padding: 20px;
      margin: 50px 20px;
      background: #fff;
      border: 1px solid #ccc;
    }

    .stla-contentarea-header h2 {
      margin: 0px;
    }

    .stla-setting-field {
      margin-top: 15px;
    }
  }

  // .gform_wrapper {
  //   display: block !important;
  // }

  .stla-responsive-area {
    background-color: $background-light;
    padding: 5px 25px 25px;
    .sk-active {
      color: var(--wp-admin-theme-color);
    }

    .stla-form-wrapper {
      background-color: #fff;
      padding: 10px;
      height: calc(100% - 40px);
      overflow: hidden;
      overflow-y: auto;
      border-radius: 3px;
      margin: auto;
      @include scrollbars(10px, #ccc, $background-light);
    }
  }
}

// spinner
.stla-content-spinner {
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

#stla-gravity-booster > .stla-content-spinner {
  height: 100vh;
}

.stla-content-spinner .components-spinner {
  width: 100px;
  height: 100px;
}

.stla-header {
  background: #ffffff;
  width: 100%;
  height: 76px;
  position: fixed;
  top: 0px;
  z-index: 3;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  overflow: hidden;

  .stla-header-logo {
    width: 90px;
    padding: 0px 10px;
    border-right: 2px solid $border-light;
  }

  .stla-header-logo img {
    max-width: 100%;
  }
  .stla-header-middle-block {
    text-align: right;
    padding: 0 15px;
  }

  .stla-header-exit {
    width: 70px;
    max-height: 76px;
    text-align: right;
    padding: 16px 15px;
    height: 76px;
    border-left: 2px solid $border-light;
    color: $border-light;
    cursor: pointer;
  }

  .stla-header-exit .dashicon {
    font-size: 25px;
    height: 25px;
    width: 25px;
  }

  .stla-form-select {
    font-size: 18px;
    line-height: 1.3;
    color: #333;
    font-weight: 400;
    padding: 0 10px;
  }
  .stla-selected-form {
    font-weight: 600;
    padding-left: 6px;
  }
  .stla-select-form-dropdown {
    vertical-align: middle;
    margin-left: 5px;
  }
  .stla-select-form-dropdown .components-button {
    background: #f7f8fa;
    border: 1px solid #ccc;
    color: #ccc;
    width: 30px;
    height: 30px;
    min-width: unset;
    padding: 0px;
  }

  .sage-ai-help-btn {
    color: $color-dark;
    font-size: 16px;
  }
  .sage-ai-help-btn .dashicon {
    margin-right: 5px;
  }
}

// panel Sidebar
.stla-panel-sidebar {
  width: 400px;
  background-color: #fff;
  position: fixed;
  top: $header-height;
  height: calc(100vh - $header-height);
  overflow-y: auto;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  @include scrollbars(10px, #ccc, $background-light);

  .stla-sidebar-panel {
    padding: 20px;
    height: auto;
    border-bottom: 1px solid $border-light;
    font-size: $text-size;
    color: $color-dark;
    box-shadow: unset !important;
  }

  .stla-sidebar-panel:hover {
    color: $color-dark;
    background-color: $background-light;
    box-shadow: unset;
  }

  .stla-sidebar-settings {
    padding: 15px 10px;
  }

  .stla-sidebar-header {
    background-color: $background-light;
    border-bottom: 1px solid $border-light;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .stla-sidebar-back-btn {
    padding: 10px;
    cursor: pointer;
  }

  .stla-sidebar-header h3 {
    padding: 20px;
    margin: 0px;
    font-size: $heading-size;
    color: $color-dark;
    width: 100%;
  }
  .stla-sidebar-header .dashicon {
    font-size: 26px;
    width: 26px;
    height: 26px;
  }
  .stla-sidebar-heading {
    border-left: 1px solid $border-light;
  }

  .stla-sidebar-custom-label {
    font-weight: 500;
    line-height: 1.4;
    text-transform: uppercase;
    display: inline-block;
    margin-bottom: calc(8px);
    padding: 0px;
    font-size: 11px;
  }

  .stla-active-section {
    border-right: 3px solid var(--wp-admin-theme-color);
    background: #f7f8fa;
  }
}

.stla-sidebar-settings {
  .stla-setting-field {
    margin-top: 15px;
  }
}

// components
.stla-background-component-card {
  .stla-full-width-btn {
    width: 100%;
    color: $color-dark;
  }
}
.stla-inline-radio-control .components-flex {
  flex-direction: row;
  gap: 10px;
  justify-content: flex-start;
}
.stla-custom-btn-group .dashicon {
  font-size: 20px;
}
.stla-color-popover .components-popover__content {
  padding: 10px;
}
// spacing
.stla-spacing-control {
  & > div {
    max-width: 100%;
  }
  .components-flex-block {
    & > div {
      max-width: 100%;
      padding-right: 10px;
    }
    .components-input-control {
      max-width: 100%;
    }
  }
}

.stla-modal-overlay {
  z-index: 999999;
  left: 90px;
}

.stla-pro-modal-container > div.components-modal__content {
  padding: 15px;
}
.stla-pro-modal {
  .stla-close-modal {
    text-align: right;
    cursor: pointer;
  }

  .stla-modal-icon {
    text-align: center;
    .dashicon {
      font-size: 40px;
      width: 40px;
      height: 40px;
    }
  }

  .stla-modal-content {
    text-align: center;

    p {
      font-size: 16px;
      line-height: 1.6;
    }

    h3 {
      font-size: 24px;
    }
  }

  .stla-modal-notice {
    background: #f7f8fa;
    font-size: 14px;
    text-align: center;
    line-height: 1.6;
    padding: 10px;
    margin: 15px 0 0;
  }
}

.stla-save-modal-container {
  .stla-modal-content {
    p {
      margin-top: 0px;
      margin-bottom: 20px;
    }
    h3 {
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }
}

.stla-help-popup-overlay {
  z-index: 999999;

  .components-modal__frame {
    width: 100%;
    height: 100%;
  }

  .stla-help-popoup-container {
    max-width: 800px;
    margin: auto;
  }
  .components-panel {
    border-top: 1px solid #ccc;
  }

  .components-panel {
    border: 0px;
    border-bottom: 1px solid #ccc;
  }

  .components-panel__body {
    padding: 0;

    & > h2 {
      margin: 0 0 5px !important;
    }
  }

  .components-panel__body.is-opened {
    padding-bottom: 15px !important;
  }

  .stla-help-search {
    margin-bottom: 20px;
  }

  .stla-help-search-results {
    margin-bottom: 15px;
  }

  .components-panel__body-title {
    &:hover {
      background: transparent;
    }

    & > .components-button {
      font-size: $subheading-size;
      padding-left: 5px;
      padding-right: 5px;
      &:focus {
        outline: none;
        box-shadow: none;
      }
      &:hover {
        background: transparent;
      }
    }
  }

  .stla-help-article-list {
    margin: 0px;

    li {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      color: $color-dark;
      font-size: $text-size;
      margin-bottom: 0px;
      color: #666666;
      a {
        color: #666666;
        text-decoration: none;
      }
      svg {
        fill: #666;
      }

      a:hover {
        color: var(--wp-admin-theme-color);
        text-decoration: underline;
        text-decoration-color: var(--wp-admin-theme-color);
      }
    }
    .stla-help-article-icon {
      margin-right: 10px;
    }
  }

  .stla-help-boxes {
    margin-top: 50px;
  }

  .stla-help-box {
    border: 1px solid #dddddd;
    border-radius: 6px;
    padding: 20px 25px 25px;
    text-align: center;

    .stla-help-box-icon svg {
      width: 70px;
      height: 70px;
      fill: #666;
    }

    .stla-help-box-content h3 {
      margin-top: 0px;
      font-size: $subheading-size;
      margin-bottom: 10px;
    }

    .stla-help-box-content p {
      font-size: $text-size;
    }
  }
}
